import { createRouter, createWebHistory } from 'vue-router'
import HomePage from '../views/HomePage.vue'
import AboutPage from '../views/AboutPage.vue'
import LoginForm from '../views/LoginForm.vue'
import ProfileCard from '../views/ProfileCard.vue'
import SalesPage from '../views/SalesPage.vue'

// definición de rutas
const routes = [
    {
        path: '/',
        name: 'Profile',
        component: ProfileCard,
        meta: {
            title: 'Miller Gutierrez | UX Developer'
        }
    },
    {
        path: '/home',
        name: 'Home',
        component: HomePage,
        meta: {
            title: 'Home Page'
        }
    },
    {
        path: '/about',
        name: 'About',
        component: AboutPage
    },
    {
        path: '/login',
        name: 'Login',
        component: LoginForm,
        meta: {
            title: 'Login Form - Miller Gutierrez'
        }
    },
    {
        path: '/sales',
        name: 'Sales',
        component: SalesPage,
        meta: {
            title: 'Ventas - Miller Gutierrez'
        }
    }
]

const router = createRouter({   // crear rutas
    history: createWebHistory('/'), // crea una historia html
    routes
})

export default router
